.education-container {
  padding: 5px 20px 20px 20px;
  background: rgba(227, 242, 253, 0.9);  /* Match Skills/Interest box */
  border: 2px solid #90caf9;            /* Same border as skills */
  margin: 1px 20px 20px 20px;
  border-radius: 10px;
  position: relative;
  width: 95%;
  color: #000;                          /* Match skills text color */
}


.education-container h2 {
  margin-top: 0;
  margin-bottom: 12px;
  position: relative;
  top: 3px;
  color: #000;
  font-weight: bold;
}

.education-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #f0f8ff;
  font-size: 1rem;
  color: #000;
}

.education-table th,
.education-table td {
  border: 1px solid #cfe0f5;
  padding: 10px;
  text-align: left;
  color: #000;
}

.education-table th {
  background-color: #e3f2fd;  /* Very light blue background */
  color: #000;            /* Medium blue text for contrast */
  font-weight: bold;
}


.education-table tr:nth-child(even) {
  background-color: #e9f2fb;
}

.education-table tr:hover {
  background-color: #d6eafc;
}

.outer-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}



/* Add two small square boxes */
.square-boxes-row {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
}



.skills-container {
  display: flex;
  justify-content: flex-start; /* üü¢ Align content to the left */
  gap: 15px;
  width: 98%;
  margin-top: 10px;
}


.area-of-interest {
  flex: 0.2;
  background: rgba(227, 242, 253, 0.9); /* Light blue transparent */
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #90caf9; /* Same as intro card border */
  color: #000; /* Match qualification text color */
  min-height: 280px; /* ‚úÖ Add this */

}

.skills {
  flex: 0.3;
  background: rgba(227, 242, 253, 0.9); /* Light blue transparent */
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #90caf9;
  color: #000;
}


.area-of-interest h3,
.skills h3 {
  margin-top: 0;
  color: #000;
}

.skills-list {
  columns: 2;
  gap: 20px;
  padding-left: 20px;
}

.skills-list li {
margin-bottom: 6px; /* Add space between each skill */
  line-height: 2.5;   /* Optional: improves readability */
}

.area-of-interest ul li {
  margin-bottom: 10px; /* Adds vertical space between list items */
  line-height: 1.5;   /* Optional: improves readability */
}

.skills-software-wrapper {
  display: flex;
  width: 98%;
  justify-content: space-between;
  gap: 20px;
}

.skills-box {
  flex: 0.5;
  background: rgba(227, 242, 253, 0.9);
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #90caf9;
  color: #000;
}

.software-box {
  flex: 0.5;
  background: rgba(227, 242, 253, 0.9);
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #90caf9;
  color: #000;
}

.skills-box h3,
.software-box h3 {
  margin-top: 0;
  color: #000;
}

.skills-box ul li,
.software-box ul li {
  margin-bottom: 8px;
  line-height: 1.6;
  list-style-type: disc;
  margin-left: 20px;
}

.software-list {
  columns: 2;
  gap: 20px;
  padding-left: 0;
}

.third-box {
  flex: 0.5;
  background: rgba(227, 242, 253, 0.9); /* Consistent with other boxes */
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #90caf9;
  color: #000;
}

.third-box h3 {
  margin-top: 0;
  color: #000;
}

.third-box ul {
  columns: 2;
  gap: 20px;
  padding-left: 20px;
}

.third-box ul li {
  margin-bottom: 8px;
  line-height: 1.6;
  list-style-type: disc;
}
