.section {
  margin-bottom: 2rem;
  padding: 1rem 2rem;
}

.section-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #facc15; /* changed from white to yellow */
  margin-bottom: 1rem;
  text-align: left;
   border-left: 4px solid #90caf9;
}


.publication-card {
  background-color: transparent;
  border-left: 4px solid #90caf9; /* teal-500 */
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  box-shadow: none;
}

.publication-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #ffffff; /* white */
}

.publication-details,
.publication-conference {
  font-size: 0.95rem;
  margin-bottom: 0.4rem;
  color: #ffffff; /* white */
}

.credential-link {
  display: inline-block;
  margin-top: 0.5rem;
  color: #facc15; /* teal for contrast */
  text-decoration: underline;
  font-weight: 500;
}

