.certifications-section {
  padding: 2rem;
  background-color: transparent;
  color: #ffffff; /* white */
}

.section-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  color: #facc15; /* yellow */
  border-left: 6px solid #facc15;
  padding-left: 0.75rem;
}

.certification-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.cert-block {
  background: transparent; /* no white */
  border-radius: 0.75rem;
  padding: 1rem 1.5rem;
  box-shadow: none; /* remove shadow */
}

.cert-subtitle {
  font-size: 1.3rem;
  color: #facc15; /* match yellow theme */
  margin-bottom: 0.75rem;
  border-bottom: 2px solid #facc15;
  padding-bottom: 0.3rem;
}

.cert-list {
  list-style-type: disc;
  margin-left: 1.5rem;
  color: #ffffff; /* white */
}

.cert-date {
  font-size: 0.9rem;
  color: #d1d5db; /* light gray */
  margin-left: 0.5rem;
}

.credential-link {
  font-size: 0.9rem;
  color: #facc15; /* highlight yellow */
  font-weight: 500;
  margin-left: 0.5rem;
  cursor: pointer;
  text-decoration: underline;
}
