.experience-section {
  padding: 2rem;
  background: transparent;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
}

.section-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
  color: #facc15;
  border-left: 6px solid #facc15;
  padding-left: 1rem;
  text-align: left;
}

.timeline-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  width: 100%;
}

.timeline-card {
  background-color: #1f2937;
  color: #fff;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.3);
  position: relative;
  width: 100%;
  max-width: 300px;
  min-width: 260px;
  flex-shrink: 0;
}

.year-badge {
  position: absolute;
  top: -12px;
  left: 1rem;
  padding: 4px 12px;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 600;
  color: #fff;
}

.year-badge.blue {
  background-color: #3b82f6;
}

.year-badge.orange {
  background-color: #f97316;
}

.timeline-info h4 {
  margin-top: 1.2rem;
  font-size: 1.1rem;
  font-weight: bold;
  color: #fff;
}

.duration {
  font-size: 0.9rem;
  color: #d1d5db;
  margin: 0.3rem 0;
}

.description {
  font-size: 0.95rem;
  color: #e5e7eb;
}

.credential-link {
  display: inline-block;
  margin-top: 0.5rem;
  color: #facc15;
  font-weight: 600;
  text-decoration: underline;
}
