html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
}


/* Background and layout */
.hero-container {
  position: relative;
  min-height: 100vh;

  background-image: url('/mybackground.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 0;
}

.hero-content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 100vh; /* change from min-height */
  overflow: hidden;
}


/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-links a {
  margin-left: 1.5rem;
  text-transform: uppercase;
  font-size: 2rem;
  text-decoration: none;
  color: white;
  position: relative;
   font-weight: bold; /* ‚Üê Make it bold */
}

.nav-links a:hover::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: white;
  left: 0;
  bottom: -3px;
}

/* Hero Section */
.hero-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 2rem;
}

.home-section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  background-color: transparent;
  padding: 0;
}



.home-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-pic {
  width: 110px; /* Reduced from 180px */
  height: 110px; /* Reduced from 180px */
  border-radius: 50%;
  border: 3px solid white;
  object-fit: cover;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
   margin-bottom: 8px; /* reduce this if needed */
}



.hero-title {
  font-size: 2.2rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.hero-subtitle {
  font-size: 1.2rem;
  letter-spacing: 1px;
}

.nav-links {
  display: flex;
  gap: 1.2rem;
}

.nav-button {
  background: none;
  border: none;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
  text-transform: uppercase;
  cursor: pointer;
  padding-bottom: 3px;
  border-bottom: 2px solid transparent;
}

.nav-button:hover {
  border-color: #facc15;
}

.nav-button.active {
  border-color: #facc15;
  color: #facc15;
}

.home-flex {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 60px;
  flex-wrap: wrap;
  transform: translateY(-30%); /* üëà Move content slightly above center */
}




.profile-left {
  text-align: center;
  max-width: 300px;
}

.intro-box {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 1000px;
  width: 100%;
  max-height: 45vh;
  overflow-y: auto;
}

.intro-card {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  padding: 20px 25px;
  color: #fff;
  font-size: 1.1rem;
  line-height: 1.6;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(10px);
  text-align: left;
}

.intro-box p {
  margin-bottom: 1rem;
}

.contact-row {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #1a1a1a;
  color: #eee;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 60px; /* üëà reduced from 30px to 12px */
  padding: 0.7rem 1.2rem;
  font-size: 0.9rem;
  border-top: 1px solid #333;
  z-index: 1000;
}


.contact-row a {
  color: #facc15;
  text-decoration: none;
  white-space: nowrap;
}

.contact-row a:hover {
  text-decoration: underline;
}

.separator {
  display: none; /* optional if you're spreading items */
}



